
import { defineComponent, ref, watch } from '@vue/composition-api';
import i18n from 'i18next';
import Company from '@/models/Company';
import { DictOption, DictItem } from '@/models/Dictionary';
import Credential from '@/models/CompanyCredential';
import UnitField from '@/views/material/info/components/UnitField.vue';

const CompanyInfoView = defineComponent({
  name: 'CompanyInfoView',
  components: {
    UnitField,
  },
  props: {
    companyInfo: {
      type: Object as () => Company,
      default: () => ({} as Company),
    },
    companyTypes: {
      type: Array as () => DictItem[],
      default: () => [] as DictItem[],
    },
    companyScale: {
      type: Array as () => DictOption[],
      default: () => [] as DictOption[],
    },
    credentials: {
      type: Array as () => Credential[],
      default: () => [] as Credential[],
    },
  },
  render() {
    const self = this as InstanceType<typeof CompanyInfoView>;
    const {
      companyInfo: {
        name,
        industry,
        logo,
        type,
        abbreviation,
        scale,
        creatorMail,
        creatorPhone,
        country,
        address,
      },
      companyTypes,
      companyScale,
      credentials,
      handleClick,
    } = self;

    return (
      <main class='company-info-view'>
        <header>
          <div class='brand-header flex'>
            <div class='logo-wrapper flex-center'>
              {self.logoError
                ? <i class='logo-placeholder iconfont iconshangjiakongbaizhuangicon'></i>
                : <img class='logo-img' src={logo}
                onerror={() => { self.logoError = true; }} /> }
            </div>
            <div class="flex-column flex-justify-center" style='margin-left: 20px;'>
              <div class='brand-name'>{name || i18n.t('supplier-backend.preview.no_name')}</div>
              <p class='brand-industry'>
                {industry || i18n.t('supplier-backend.preview.no_industry')}
              </p>
            </div>
          </div>
        </header>
        <section class='section-base-info'>
          <el-divider content-position='left'>{ i18n.t('supplier-backend.company-brief.company_info') }</el-divider>
          <div>
            <div class='info-item'>
              <span class='info-title'>{ i18n.t('supplier-backend.company-brief.business_type')}:</span>
              <span>
                {(companyTypes ? companyTypes.find(e => e.itemValue === type)?.label : '') || i18n.t('supplier-backend.company-brief.empty')}
              </span>
            </div>
            <div class='info-item'>
              <span class='info-title'>{ i18n.t('supplier-backend.company-brief.company_alias')}:</span>
              <span>{abbreviation || i18n.t('supplier-backend.company-brief.empty')}</span>
            </div>
            <div class='info-item'>
              <span class='info-title'>{ i18n.t('supplier-backend.company-brief.company_size')}:</span>
              <span>{(companyScale ? companyScale.find(e => e.value === scale)?.name : '') || i18n.t('supplier-backend.company-brief.empty')}</span>
            </div>
          </div>
        </section>

        <section class='section-contact-info'>
          <el-divider content-position='left'>{ i18n.t('supplier-backend.company-brief.contact_us') }</el-divider>
          <div>
            <div class='info-item'>
              <span class='info-title'>{ i18n.t('supplier-backend.company-brief.contact_email')}:</span>
              <span>{creatorMail || i18n.t('supplier-backend.company-brief.empty')}</span>
            </div>
            <div class='info-item'>
              <span class='info-title'>{ i18n.t('supplier-backend.company-brief.contact_info')}:</span>
              <span>{creatorPhone || i18n.t('supplier-backend.company-brief.empty')}</span>
            </div>
            <div class='info-item'>
              <span class='info-title'>{ i18n.t('supplier-backend.company-brief.address')}:</span>
              <span class='info-content'>
                {address}{address && country ? ', ' : ''}<UnitField type="country" value={country} />
                <span>{(!address && !country) ? i18n.t('supplier-backend.company-brief.empty') : ''}</span>
              </span>
            </div>
          </div>
        </section>

        <section class='section-credentials'>
          <el-divider content-position='left'>{ i18n.t('supplier-backend.company-brief.credentials') }</el-divider>
          <div class="credentials-list">
            { credentials?.length > 0 ? credentials.map((cred, index) => (
              <div class='cred-item'>
                <div class="cred-img-wrap"
                  onClick={ () => handleClick(index) }>
                  <div class="cred-img-zoom-veil">
                    <i class="iconfont el-icon-zoom-in"></i>
                  </div>
                  <base-image
                    fit="contain"
                    thumb={{ width: 220, height: 169 }}
                    style={{ height: 200, backgroundColor: 'rgb(245, 245, 245)' }}
                    src={ cred.imgUrl }
                    default-image=''
                    class="cred-img">
                  </base-image>
                </div>
              </div>
            )) : <div class='credentials-empty'>{ i18n.t('supplier-backend.company-brief.credentials_empty') }</div>
            }
          </div>
        </section>
      </main>
    );
  },
  setup(props, { root: { $ImageViewer } }) {
    async function handleClick(index: number) {
      await $ImageViewer.show(props.credentials.map(cred => cred.imgUrl), {
        index,
        targetElems: '.cred-img-wrap',
      });
    }
    return {
      logoError: ref(false),
      handleClick,
    };
  },
});
export default CompanyInfoView;
