
import {
  defineComponent, ref, Ref, reactive,
} from '@vue/composition-api';
import i18n from 'i18next';
import useLoader from '_/composables/use-loader';
import axios from '@/api/axios';
import errorHandler from '@/utils/error-handler';
import { getDictByType, getDictionaryByTypeCode } from '@/loaders/dictionary';
import { DictOption, DictItem } from '@/models/Dictionary';
import tryParseJson from '_/try-parse-json';
import CompanyInfoView from './CompanyInfoView.vue';
import CompanyInfoForm from './CompanyInfoForm.vue';

const companyInfo = defineComponent({
  name: 'CompanyInfo',
  props: {
    companyId: {
      type: String,
      required: true,
    },
    editable: {
      type: String,
      default: 'false',
    },
  },
  render() {
    const self = this as InstanceType<typeof companyInfo>;
    const {
      company: { info: company, credentials },
      companyTypes,
      companyScale,
      mode,
      changeMode,
      submitHandle,
      editable,
    } = self;
    const companyInfoView = <CompanyInfoView companyInfo={ company }
        credentials={ credentials }
        companyTypes={ companyTypes} companyScale={ companyScale }/>;
    const companyInfoForm = <CompanyInfoForm companyInfo={ company } ref="companyInfoForm"
        credentials={ credentials }
        companyTypes={ companyTypes } companyScale={ companyScale }
        onCancel={ () => changeMode('view') } onSubmit={ submitHandle }/>;
    const editBar = <div class="edit-bar">
        <el-button round size="medium" type="primary"
          onClick={ () => changeMode('edit') }
        >{ i18n.t('public.form.edit') }</el-button>
      </div>;
    return (
      <main class="company-info-container">
        { editable === 'true' && mode === 'view' && editBar }
        <div class="company-info-content">
          { mode === 'view' ? companyInfoView : companyInfoForm }
        </div>
      </main>
    );
  },
  setup(props, { root }) {
    function getCompanyInfo() {
      const data = ref();
      axios({
        method: 'get',
        url: '/authcenter/company/get/info/id',
        params: {
          id: props.companyId,
        },
      }).then(res => {
        data.value = res.data;
      });
      return data;
    }
    function getCompanyQualification() {
      const data = ref<Credential[]>();
      axios({
        method: 'get',
        url: '/authcenter/company/credentials',
        params: {
          id: props.companyId,
        },
      }).then(res => {
        const result = tryParseJson(res.data);
        data.value = result;
      });
      return data;
    }

    const company = reactive({
      info: getCompanyInfo(),
      credentials: getCompanyQualification(),
    });
    company.info = {};
    const companyTypes = useLoader(() => {
      return getDictionaryByTypeCode('company', i18n.language);
    }, {}, {
      initialValue: [] as DictItem[],
    });
    const companyScale = useLoader(() => {
      return getDictByType('scale');
    }, {}, {
      initialValue: [] as DictOption[],
    });
    const mode: Ref<string> = ref('view');

    function changeMode(newMode: string) {
      mode.value = newMode;
    }
    function submitHandle() {
      company.info = getCompanyInfo();
      company.credentials = getCompanyQualification();
      changeMode('view');
    }
    return {
      company,
      companyTypes,
      companyScale,
      mode,
      changeMode,
      submitHandle,
    };
  },
});
export default companyInfo;
