











































































































































import {
  defineComponent, ref, Ref, reactive, computed,
} from '@vue/composition-api';
import i18n from 'i18next';
import { ElForm } from 'element-ui/types/form';
import AppError from '@/utils/error';
import Company from '@/models/Company';
import { DictOption, DictItem } from '@/models/Dictionary';
import FormSubmitButtons from '@/components/form/FormSubmitButtons.vue';
import { updateCompanyInfo, updateCompanyCredentials } from '@/api/company';
import errHandler from '@/utils/error-handler';
import DictionarySelector from '@/components/dictionary/DictionarySelector.vue';
import DictionaryCountrySelector from '@/components/dictionary/DictionaryCountrySelector.vue';
import validator, { validateFormFields } from '@/utils/validators/rules-validation';
import UploadCompanyLogo from '@/views/profile/info/components/upload/UploadCompanyLogo.vue';
import UploadCompanyCredentials from '../components/UploadCompanyCredentials.vue';

const CompanyInfoForm = defineComponent({
  name: 'CompanyInfoForm',
  components: {
    FormSubmitButtons,
    DictionarySelector,
    DictionaryCountrySelector,
    UploadCompanyLogo,
    UploadCompanyCredentials,
  },
  props: {
    companyInfo: {
      type: Object as () => Company,
    },
    companyTypes: {
      type: Array as () => DictItem[],
      default: () => [] as DictItem[],
    },
    companyScale: {
      type: Array as () => DictOption[],
      default: () => [] as DictOption[],
    },
    credentials: {
      type: Array as () => Credential[],
      default: () => [] as Credential[],
    },
  },
  setup(props, { emit }) {
    const _companyInfo: Ref<Company> = ref(JSON.parse(JSON.stringify(props.companyInfo)));
    const _credentials = ref(JSON.parse(JSON.stringify(props.credentials)));
    if (!_credentials.value) {
      _credentials.value = [];
    }
    const _credentialsDisplay = computed(() => {
      // 预留一个空的上传位置
      if (_credentials.value.length < 6) {
        return _credentials.value.concat({});
      }
      return _credentials.value;
    });
    const formRef = ref<ElForm>(null);
    const updateInfo: {
      [prop: string]: string | number
    } = {};
    let credentialsChange = false;

    function cancel() {
      emit('cancel');
    }
    async function submit() {
      // 修改公司信息
      try {
        await validateFormFields(formRef.value as ElForm);
        await updateCompanyInfo({
          id: _companyInfo.value.id,
          ...updateInfo,
        });
        if (credentialsChange) {
          await updateCompanyCredentials({
            id: _companyInfo.value.id,
            credentials: JSON.stringify(_credentials.value),
          });
        }
        emit('submit');
      } catch (err) {
        errHandler(err);
      }
    }

    function changeCompanyInfo(field: string, value: any) {
      updateInfo[field] = value;
    }
    function validateFormField(field: string, value: any) {
      if (typeof (formRef.value as any).validate === 'function') {
        (formRef.value as any).validate((valid: boolean, invalidFields: any) => {
          // 判断该属性是否在校验失败的field对象里面，如果校验通过则调用changeCompanyInfo更新updateInfo
          if (!invalidFields[field]) {
            changeCompanyInfo(field, value);
            // emit('validate-field', { field, value });
          }
        });
      }
    }
    function changeCredentials(index: string, imgUrl: string) {
      if (_credentials.value[index]) {
        _credentials.value[index].imgUrl = imgUrl;
      } else {
        if (_credentials.value?.length >= 6) {
          throw new AppError('最多只允许上传6张资质图片');
        }
        _credentials.value.push({
          imgUrl,
        });
      }
      credentialsChange = true;
    }
    function deleteCredentials(index: string) {
      _credentials.value.splice(index, 1);
      credentialsChange = true;
    }

    const rules = {
      name: [
        validator.spaceTrim(),
        ...validator.charLengthRange(2, 64),
        validator.requirement(),
      ],
      creatorFirstName: [
        validator.spaceTrim(),
        validator.charTypeNormal1(),
        validator.charLengthMax(20),
      ],
      creatorLastName: [
        validator.spaceTrim(),
        validator.charTypeNormal1(),
        validator.charLengthMax(20),
      ],
      abbreviation: [
        validator.spaceTrim(),
        ...validator.charLengthRange(4, 20),
      ],
      creatorPosition: [
        validator.spaceTrim(),
        validator.charLengthMax(64),
      ],
      creatorPhone: [
        validator.charLengthMax(64),
        validator.charTypeDigitAndHyphen(),
      ],
      address: [
        validator.spaceTrim(),
        validator.charLengthMax(100),
      ],
      industry: [
        validator.spaceTrim(),
        validator.charLengthMax(200),
      ],
    };

    return {
      _companyInfo,
      _credentials,
      _credentialsDisplay,
      cancel,
      submit,
      formRef,
      rules,
      validateFormField,
      changeCompanyInfo,
      changeCredentials,
      deleteCredentials,
    };
  },
});
export default CompanyInfoForm;
